import React, {useState} from 'react';
import styled from 'styled-components';
import WindowMobile from './WindowMobile';

const StyledBurger = styled.div `
    width: 2rem;
    height: 2rem;
    position: fixed;
    top: 15px;
    right: 20px;
    z-index: 100;
    display: flex;
    justify-content: space-around;
    flex-flow: column nowrap;

  div {
    width: 2rem;
    height: 0.25rem;
    background-color: ${ ({
  open}) => open ? '#fff' : '#333'};
    border-radius: 10px;
    transform-origin: 1px;

    &:nth-child(1) {
      transform: ${ ({
    open}) => open ? 'rotate(-225deg) translate(-1.125rem, -1.125rem)' : 'rotate(0) translate(0, 0)'};
      transition: transform 0.125s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    }
    &:nth-child(2) {
      transition: opacity 0.100s 0.150s ease;
      transform: ${ ({
      open}) => open ? 'translateX(300%)' : 'translateX(0)'};
      opacity: ${ ({
        open}) => open ? 0 : 1};
    }
    &:nth-child(3) {
      transition: transform 0.125s cubic-bezier(0.68, -0.55, 0.265, 1.55);
      transform: ${ ({
          open}) => open ? 'rotate(225deg) translate(-1.125rem, 1.125rem)'  : 'rotate(0) translate(0, 0)'};
    }
  }
`;

          const Burger = () => {
            const [open, setOpen] = useState(false)

            return (<> < StyledBurger open = {
              open
            }
            onClick = {
              () => setOpen(!open)
            } > <div/>
            <div/>
            <div/>
          </StyledBurger>
          <WindowMobile open={open}/>
        </>)
          }
          export default Burger
